import React,{useState,useEffect} from 'react';
import '../../components/Style/Style.css';
import {connect} from 'react-redux';
import util from '../../actions/index';
import {removeProductToCart,incrementCartQuantity,decrementCartQuantity} from "../../actions/index";

function Cart(props) {
    const [count, setCount] = useState(1);

    const total = util.formatCurrency(props.cartItems.reduce((a, c) => a + c.price * c.count, 0));
    return (
        <div className='cart'>
            <div className='container'>
                <div className='main'>
                    <div className='cart-first'>
                        <div className='cart-first-item'>
                            <h2>Shoping cart ({props.cartItems.length})</h2>
                            <div className='msg-list'></div>
                            <div className='btn-all'></div>
                        </div>
                        <div className='cart-first-item'>
                            <div className='products'>
                                <ul>
                                    {props.cartItems.length == 0 ?
                                            <li>
                                                <h3>Cart is empty</h3>
                                                <p>No items in cart</p>
                                            </li>
                                        :
                                        props.cartItems.map(product => (
                                            <li>
                                                <div className='product'>
                                                    <button className='delete' onClick={() => props.removeProductToCart(props.cartItems,product)}><i class="far fa-trash-alt"></i></button>
                                                    <div className='image'>
                                                        <img src={product.image}/>
                                                    </div>
                                                    <div className='content'>
                                                        <div className='title'>
                                                            <p>{product.title}</p>
                                                        </div>
                                                        <div className='color'>
                                                            <p><b>Color</b>: Black</p>
                                                        </div>
                                                        <div className='price'>
                                                            <p><b>${product.price}</b></p>
                                                        </div>
                                                    </div>
                                                    <div className='options'>
                                                        <div className='buttons'>
                                                            <input type='submit' onClick={() => props.decrementCartQuantity(product.id)} value='-'/>
                                                            <p>{count}</p>
                                                            <input type='submit' onClick={() => props.incrementCartQuantity(product.id)} value='+'/>
                                                        </div>
                                                    </div>
                                                </div>
                                            </li>
                                        ))}
                                </ul>
                            </div>
                        </div>
                        <div className='cart-first-item'>
                            <div className="card-container ">
                                <div className="payment-cabinet">
                                    <h3>
                                        Payment methods
                                    </h3>
                                    <ul>
                                        <li><img src="//ae01.alicdn.com/kf/HTB1fNUEaNrvK1RjSsze761ObFXaX.png"/></li>
                                        <li><img src="//ae01.alicdn.com/kf/HTB1xDsCaODxK1Rjy1zc761GeXXae.png"/></li>
                                        <li><img src="//ae01.alicdn.com/kf/HTB1RS7DaOHrK1Rjy0Fl763saFXax.png"/></li>
                                        <li><img src="//ae01.alicdn.com/kf/HTB17y7yaIvrK1Rjy0Fe763TmVXaS.png"/></li>
                                        <li><img src="//ae01.alicdn.com/kf/HTB19ZUGaJzvK1RkSnfo762MwVXaw.png"/></li>
                                        <li><img src="//ae01.alicdn.com/kf/HTB1OzoFaOYrK1Rjy0Fd763CvVXaQ.png"/></li>
                                    </ul>
                                </div>
                                <div className="buyer-protection">
                                    <h3>
                                          Buyer Protection
                                    </h3>
                                    <p>
                                        You can claim your money back if you have not received the item or it does not match the description.
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='cart-sec'>
                        <div className='price'>
                            <h2>Summary</h2>
                            <div className='table-options'>
                                <p>Partial total</p>
                                <p>{total}</p>
                            </div>
                            <div className='table-options'>
                                <p>Shiping</p>
                                <p>US $ 0.00</p>
                            </div>
                            <div className='total-price'>
                                <div className='content'>
                                    <h4>Total</h4>
                                    <h4>
                                        {total}
                                    </h4>
                                </div>
                                <div className='button'>
                                    <button>BUY ({total})</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
const mapStateToProps = state => ({
    cartItems:state.shop.items,
});

export default connect(mapStateToProps,{removeProductToCart,incrementCartQuantity,decrementCartQuantity})(Cart);
