import React,{useState} from 'react';
import {Products,Filter} from '../components/CategoriesComponents.js/index';
import * as products from '../components/Data/products.json';
import {useParams} from 'react-router-dom';

function Categories() {
    const [displayMode, setDisplayMode] = useState('grid');
    const {slug} = useParams()
    const productsFilter = products.data.filter(pr => pr.category == slug);

    const [productsAll, setProductsAll] = useState(productsFilter);
    
    return (
        <div className='categories-page'>
            <div className='container'>
                <div className='main'>
                    <div className='products'>
                        <div className='filters'>
                            <Filter setDisplayMode={setDisplayMode} displayMode={displayMode}/>
                        </div>
                        <Products displayMode={displayMode} products={productsAll} />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Categories
