import React,{useState} from 'react';
import {Products,Filter} from '../components/CategoriesComponents.js/index';
import * as products from '../components/Data/products.json';
import {useParams} from 'react-router-dom';


function Search() {
    const [displayMode, setDisplayMode] = useState('grid');
    const {query} = useParams()
    const productsFilter = products.data.filter(pr => pr.category.includes(query));

    console.log(productsFilter)
    return (
        <div className='categories-page'>
            <div className='container'>
                <div className='main'>
                    <div className='products'>
                        <h3 className='mb-3 mt-3'>Your search '{query}' </h3>
                        <p>({productsFilter.length} Results)</p>

                        {productsFilter.length == 0 ? 
                            <div className='query-help'>
                                <h3 className='mb-3 mt-3'>Sorry, your search '{query}' did not match any products. Please try again. </h3>
                            </div>    
                        :
                            <>
                                <div className='filters'>
                                    <Filter setDisplayMode={setDisplayMode} displayMode={displayMode}/>
                                </div>
                                <Products displayMode={displayMode} products={productsFilter} />
                            </>
                        }
                        
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Search
