export const ADD_PRODUCT_TO_CART = 'ADD_PRODUCT_TO_CART';
export const REMOVE_PRODUCT_FROM_CART = 'REMOVE_PRODUCT_FROM_CART';
export const INCREMENT_CART_ITEM_QUANTITY = 'INCREMENT_CART_ITEM_QUANTITY';
export const DECREMENT_CART_ITEM_QUANTITY = 'DECREMENT_CART_ITEM_QUANTITY';
export const FETCH_PRODUCTS_SUCCESS = 'FETCH_PRODUCTS_SUCCESS';


export const fetchProductsSuccess = products => ({
    type: FETCH_PRODUCTS_SUCCESS,
    payload: { products }
});

export const addProductToCart = (items, product) => (dispatch) => {
    const cartItems = items.slice();
    let productAlreadyInCart = false;
  
    cartItems.forEach((cp) => {
      if (cp.id === product.id) {
        cp.count += 1;
        productAlreadyInCart = true;
      }
    });
  
    if (!productAlreadyInCart) {
      cartItems.push({ ...product, count: 1 });
    }
    dispatch({ type: ADD_PRODUCT_TO_CART, payload: { cartItems } });
  };

export const removeProductToCart = (items, product) => (dispatch) => {
    const cartItems = items.slice().filter((a) => a.id !== product.id);
    dispatch({ type: REMOVE_PRODUCT_FROM_CART, payload: { cartItems } });
};

export const incrementCartQuantity = id => {
  return {
    type: INCREMENT_CART_ITEM_QUANTITY,
    id,
  };
};

export const decrementCartQuantity = id => {
  return {
    type: DECREMENT_CART_ITEM_QUANTITY,
    id,
  };
};

export default {
  formatCurrency: function (num) {
      return '$' + Number(num.toFixed(1)).toLocaleString() + ' ';
  }
}